@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.main {
    display: flex;
    justify-content: center;
    padding: 50px;
}

.tarefasWrapper {
    background: #fff;
    padding: 24px;
    width: 100%;
    max-width: 400px;
    min-height: 400px;
    box-shadow: var(--shadow);
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    grid-gap: 24px;
    gap: 24px;
}

.tarefasWrapper h1 {
    display: none;
}


  .listaTarefas {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }


  .listaVazia {
    background-color: var(--tarefasBG);
    border: 1px dashed var(--dash);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    transition: all 0.6s cubic-bezier(0.25, 1, 0.5, 1);
  }

  .listaVaziaOculta,
  .liOculta {
      opacity: 0;
      transform: translateY(100px);
  }

  .listaVazia span {
      font-size: 14px;
      color: var(--c2);
  }




input {
    box-shadow: var(--shadow);
    outline: none;
    background-color: #fff;
    border-radius: 2px;
    color: var(--c1);
    display: block;
    box-sizing: border-box;
    min-height: 48px;
}


#submitBtn {
    border-radius: 50%;
    background-color: var(--green);
    width: 48px;
    height: 48px;
    display: block;
    position: relative;
    box-sizing: border-box;
  }

#submitBtn img {
    width: 24px;
    height: 24px;
    display: block;
    position: absolute;
    box-sizing: border-box;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  form {
      display: grid;
      grid-template-columns: 1fr auto;
      grid-gap: 16px;
      align-items: start;
  }

  .tarefas {
    display: grid;
    grid-gap: 8px
}

.tarefas li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 32px;
    background-color: var(--tarefasBG);
    padding: 0 16px;
    font-size: 14px;
    color: var(--c2);
    border-radius: 2px;
    transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1);
}

.tarefas li:hover {
    background-color: var(--hoverTarefa);
}

.deleteBtn img {
    height: 16px;
    width: auto;
    pointer-events: none;
}


.deleteBtn {
    cursor: pointer;
    display: flex;
    align-items: center;
    outline: none;
}

.deleteBtn:hover {
    -webkit-filter: saturate(0.8);
            filter: saturate(0.8);
}


:root {
  --shadow: 0 1px 4px rgba(0,0,0,.25);
  --c1: #303030;
  --c2: #606060;
  --c3: #909090;
  --green: #40D9B8;
  --tarefasBG: #f2f2f2;
  --dash: #CCCCCC;
  --hoverTarefa: #f5f5f5;
}

body, html {
  margin: 0;
  padding: 0;
  outline: none;
}

input {
	display:block;
	box-sizing: border-box;
	position:relative;
	border: none;
	width: 100%;
  outline: none;
  padding: 0 16px;
  font-size: 16px;
}

button[type="submit"] {
	cursor: pointer;
	border: none;
  outline: none;
}


input[type=submit] {
  
  border: 0;
  display: block;
  height: _the_image_height;
  width: _the_image_width;
}

span, li, input {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
}

::-webkit-input-placeholder {
	color: #909090;
	color: var(--c3);
	font-size: 16px;
}

:-ms-input-placeholder {
	color: #909090;
	color: var(--c3);
	font-size: 16px;
}

::placeholder {
	color: #909090;
	color: var(--c3);
	font-size: 16px;
}

div {
  position: relative;
  display: block;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
}


ul {
  list-style: none;
  list-style-position: outside;
  margin: 0;
  padding: 0;
}
